import React, {useState, useEffect, useCallback} from 'react';
import {useTranslation} from 'react-i18next';

function Setting() {
    const {t, i18n} = useTranslation()
    
    const enStyle = (i18n.language == 'en') ? 'info-select' : ''
    
    const zhStyle = (i18n.language == 'zh') ? 'info-select' : ''


    return (
        <div className="index-content">
            <div className="setting-item">
                <div className="item-title">
                    {t("languagesetting")}
                </div>
                <div className="item-info">
                    <span className={enStyle}
                        onClick={() => i18n.changeLanguage(i18n.language = 'en')}>{t('English')}</span>  <span
                    className={zhStyle}
                    onClick={() => i18n.changeLanguage(i18n.language = 'zh')}>{t('Chinese')}</span>
                </div>
            </div>    
        </div>
        )
}

export default Setting;